*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Helvetica Neue", -apple-system, BlinkMacSystemFont, "Segoe UI",
    Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", sans-serif;
}

nav {
  display: flex;
  align-items: center;
}

button {
  border: 2px solid steelblue;
  background-color: #fff;
  color: steelblue;
  font-weight: 600;
  padding: 8px 16px;
}

button:hover {
  background-color: steelblue;
  color: white;
  cursor: pointer;
}

nav {
  border-bottom: 1px solid #e5e5e5;
  padding: 12px 0;
}

nav h1,
nav button {
  margin: 0 20px;
}

nav h1 {
  font-size: 1rem;
}

#admin_panel {
  max-width: 96vw;
  margin: 20px auto;
}

#admin_panel_header {
  margin: 30px 0;
}

#admin_panel_header span {
  font-weight: 500;
  font-size: 14px;
}

[data-reach-tabs] {
  display: grid;
  grid-template-columns: 15% 85%;
  grid-template-areas: "options" "window";
  grid-gap: 20px;
}

[data-reach-tab-list] {
  display: flex;
  flex-direction: column;
  border-right: 1px solid #e5e5e5;
  background-color: #fff !important;
  padding: 0 10px;
}

[data-reach-tab] {
  padding: 24px 10px !important;
  border-bottom: 1px solid #e5e5e5 !important;
  text-align: right;
}

table {
  background-color: #f7f7f7;
}

thead th {
  font-size: 16px;
  font-weight: 600;
  padding: 10px 10px 10px 3px;
}

thead tr:first-child th {
  border-bottom: 1px solid #e5e5e5;
}

thead tr:last-child th:hover {
  color: steelblue;
}

thead th select {
  margin: 0 8px;
}

tbody tr {
  font-size: 14px;
  font-weight: 300;
  letter-spacing: 0.2px;
}
tbody tr:nth-child(odd) {
  background-color: white;
}

tbody tr td {
  padding: 5px 10px 5px 3px;
}

.pagination {
  margin-top: 10px;
}

[data-reach-dialog-content] {
  position: relative;
}

[data-reach-dialog-content] .close-button {
  position: absolute;
  right: 10px;
  top: 10px;
  font-size: 18px;
  padding: 4px 10px;
}

[data-reach-dialog-content] button {
  margin: 4px 8px;
}

.modal__name {
  margin: 0.5rem 0;
}
.modal__email {
  color: #444;
  margin: 0 0 2rem;
}

/**************************
//
// Add Subscriber Form
//
/**************************/

#add-sub-form {
  max-width: 700px;
  position: relative;
  z-index: 2;
  margin: 2rem;
  transition: opacity 0.3s ease-in;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
#add-sub-form.locked {
  opacity: 0.4;
  pointer-events: none;
}

#add-sub-form fieldset {
  margin: 0 0 2rem;
  padding: 1rem;
  color: #1a202c;
  border: none;
  background-color: #e2e8f0;
  position: relative;
  border-radius: 4px;
}
#add-sub-form legend {
  font-weight: bold;
  font-size: 1rem;
  padding: 0 8px 2px;
  background-color: #ffffff;
}

.fieldset-inputs {
  display: flex;
  flex-wrap: wrap;
}

#add-sub-form p {
  color: #4a5568;
  font-size: 12px;
  margin: 8px 0;
  line-height: 16px;
}

#add-sub-form label {
  font-size: 12px;
  font-weight: bold;
  color: #363636;
  padding: 3px 0;
  margin-bottom: 2rem;
  display: block;
}

#add-sub-form label.full-width {
  width: 98%;
  flex: 0 0 98%;
}

.validation {
  display: none;
  margin-top: 5px;
}

#add-sub-form input {
  margin-right: 24px;
  width: 100%;
  font-size: 14px;
  line-height: 22px;
  font-weight: normal;
  color: #1a202c;
  padding: 4px 8px;
  border: none;
  background-color: #f9f9f9;
  border-radius: 4px;
  margin-top: 5px;
  max-width: 90%;
}

#add-sub-form input[type="radio"] {
  transform: scale(0);
  position: absolute;
}

#add-sub-form input[type="radio"] + label {
  border: 2px solid #1a202c;
  display: inline-block;
  padding: 8px 24px;
  border-radius: 4px;
  text-align: center;
  margin-right: 12px;
  font-size: 14px;
  color: #1a202c;
  margin-bottom: 0;
}

#add-sub-form input[type="radio"] + label:hover {
  background-color: #1a202c;
  color: white;
  cursor: pointer;
}

#add-sub-form input[type="radio"]:checked + label {
  background-color: #1a202c;
  color: white;
}

#add-sub-form input[type="text"]:required:valid,
input[type="email"]:required:valid {
  border: 1px solid #48bb78;
}
#add-sub-form input[type="text"]:required:invalid,
input[type="email"]:required:invalid {
  border: 1px solid #f56565;
}

#add-sub-form input[type="text"]:required:invalid + .validation,
input[type="email"]:required:invalid + .validation {
  display: block;
}

#add-sub-form input[type="text"]:required:placeholder-shown,
input[type="email"]:required:placeholder-shown,
input[type="email"]:required:focus {
  border: none;
}

#add-sub-form button {
  border: 2px solid #1a202c;
  display: inline-block;
  padding: 12px 36px;
  border-radius: 4px;
  text-align: center;
  font-size: 14px;
  background-color: rgba(255, 255, 255, 0);
  font-weight: bold;
  margin: 0 auto 20px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

#add-sub-form button:hover {
  background-color: #1a202c;
  color: white;
  cursor: pointer;
}

#add-sub-form h3 {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

#add-sub-form input[type="submit"] {
  width: auto;
  border: 2px solid currentColor;
  font-weight: 600;
  padding: 8px 24px;
}

#add-sub-form input[type="submit"]:hover {
  background-color: #1a202c;
  color: white;
  cursor: pointer;
}

@media (min-width: 640px) {
  #add-sub-form label.half-width {
    width: calc(calc(100% - 24px) / 2);
    flex: 1 0 45%;
    margin-right: 12px;
  }
  #add-sub-form label.third-width {
    width: calc(calc(100% - 36px) / 3);
    flex: 1 0 31%;
    margin-right: 12px;
  }
}

#edit-form input,
#edit-form label {
  display: block;
  font-size: 14px;
}

#edit-form input {
  margin: 4px 0 1rem;
  padding: 4px;
}

@media (min-width: 1000px) {
  .fieldset-comment {
    position: absolute;
    top: 0;
    left: -240px;
    max-width: 200px;
  }
  #add-sub-form p {
    text-align: right;
  }

  #add-sub-form button {
    margin: auto;
    position: absolute;
    bottom: 1rem;
    right: 12px;
  }
}
